import { useState, useEffect } from 'react';
import { useParams, useLocation } from 'react-router-dom';
// @mui
import { Container, Card, Table, TableBody, TableRow, TableCell, Typography, Box, Divider, Grid } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import Image from '../../components/Image';
import LogoLoader from '../../components/LogoLoader'; // Import new loader

// sections
import { InvoiceToolbar } from '../../sections/@dashboard/invioce/data';
import axios from '../../utils/axios';
import { fCurrency } from '../../utils/formatNumber';
import useAuth from '../../hooks/useAuth';

// ----------------------------------------------------------------------

export default function DataInvoice() {
  const { themeStretch } = useSettings();
  const { name = '' } = useParams();
  const { state } = useLocation();
  const [trans, setTrans] = useState(state?.transaction || null); // Prioritize state
  const [load, setLoad] = useState(!state?.transaction); // Only load if no state
  const { setting } = useAuth();

  useEffect(() => {
    if (!trans) {
      setLoad(true);
      axios.get(`/api/data/trans/${name}/secure`)
        .then(response => {
          setTrans(response.data?.trans);
          setLoad(false);
        })
        .catch(() => setLoad(false));
    }
  }, [name, trans]);

  if (load) {
    return (
      <Box sx={{ height: '100vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <LogoLoader />
      </Box>
    );
  }

  return (
    <Page title="Receipt: Transaction Details">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Transaction Receipt"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.general.app },
            { name: 'Transactions', href: PATH_DASHBOARD.general.transactions },
            { name: 'Receipt' },
          ]}
        />

        {!load && !trans && (
          <Box sx={{ py: 10, textAlign: 'center' }}>
            <Typography variant="h5" paragraph>Transaction Not Found</Typography>
            <Typography variant="body2" sx={{ color: 'text.secondary' }}>
              We couldn't find details for this transaction. It might have been deleted or the link is invalid.
            </Typography>
          </Box>
        )}

        {trans && (
          <>
            <InvoiceToolbar invoice={trans} />
            <Card sx={{ pt: 5, px: 5, pb: 5 }}>
              <Box sx={{ textAlign: 'center', mb: 5 }}>
                <Label
                  variant="filled"
                  color={trans?.plan_status === 1 ? 'success' : 'error'}
                  sx={{ textTransform: 'uppercase', mb: 2 }}
                >
                  {trans?.plan_status === 1 ? 'Successful' : (trans?.plan_status === 0 ? 'Processing' : 'Failed')}
                </Label>
                <Typography variant="h3" sx={{ fontWeight: 'bold' }}>
                  ₦{fCurrency(trans?.amount || 0)}
                </Typography>
                <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                  {trans?.plan_date || trans?.updated_at || trans?.created_at || 'N/A'}
                </Typography>
              </Box>

              <Divider sx={{ mb: 3, borderStyle: 'dashed' }} />

              <Typography variant="overline" sx={{ color: 'text.secondary', mb: 3, display: 'block' }}>
                Transaction Details
              </Typography>

              <Grid container spacing={2}>
                <Grid item xs={6}><Typography variant="body2" sx={{ color: 'text.secondary' }}>Reference</Typography></Grid>
                <Grid item xs={6}><Typography variant="subtitle2" sx={{ textAlign: 'right' }}>{trans?.transid || 'N/A'}</Typography></Grid>

                <Grid item xs={6}><Typography variant="body2" sx={{ color: 'text.secondary' }}>Beneficiary</Typography></Grid>
                <Grid item xs={6}><Typography variant="subtitle2" sx={{ textAlign: 'right' }}>{trans?.beneficiary || trans?.plan_phone || trans?.phone || trans?.phone_number || 'N/A'}</Typography></Grid>

                <Grid item xs={6}><Typography variant="body2" sx={{ color: 'text.secondary' }}>Network/Service</Typography></Grid>
                <Grid item xs={6}><Typography variant="subtitle2" sx={{ textAlign: 'right' }}>{trans?.network || trans?.disco_name || trans?.cable_name || 'N/A'}</Typography></Grid>

                <Grid item xs={6}><Typography variant="body2" sx={{ color: 'text.secondary' }}>Description</Typography></Grid>
                <Grid item xs={6}><Typography variant="subtitle2" sx={{ textAlign: 'right' }}>{trans?.plan_name || trans?.message || 'Transaction Details'}</Typography></Grid>

                <Grid item xs={6}><Typography variant="body2" sx={{ color: 'text.secondary' }}>Balance Before</Typography></Grid>
                <Grid item xs={6}><Typography variant="subtitle2" sx={{ textAlign: 'right' }}>₦{fCurrency(trans?.oldbal || 0)}</Typography></Grid>

                <Grid item xs={6}><Typography variant="body2" sx={{ color: 'text.secondary' }}>Balance After</Typography></Grid>
                <Grid item xs={6}><Typography variant="subtitle2" sx={{ textAlign: 'right' }}>₦{fCurrency(trans?.newbal || 0)}</Typography></Grid>
              </Grid>

              <Divider sx={{ mt: 5, mb: 2 }} />

              <Grid container>
                <Grid item xs={12} sx={{ mb: 2 }}>
                  <Typography variant="subtitle2">Need Help?</Typography>
                  <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                    Contact support at {setting?.general?.app_email || 'support@abokisub.com'}
                  </Typography>
                </Grid>
              </Grid>
            </Card>
          </>
        )}
      </Container>
    </Page>
  );
}

